import React,{useRef, useState, useCallback, useEffect} from "react";
import styled from "styled-components";
import Alert from "../util_components/Alert";
// axios      Alert   
import axiosCustom from '../util_components/axiosCustom';
import { useNavigate } from "react-router-dom";

const Signup_Overlay = styled.div`
    //        div , z-index : alert (alert index: 200)
    position: absolute;
    z-index: 105;
    width: 100vw;
    height: 100vh;

    /* pc & mobile x scroll OFF~ (body overflow-x: clip) */
    overflow-x: clip;
    background-color: #18181A;
`

const Signup_Container_div = styled.div`
    width: 750px;
    height: 750px;
    
    // absolute div    
    /* c.f
        + div    -> margin: 0 auto;
        + text    -> text-align: center;
    */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);

    display: flex;
    justify-content: center;
    align-items: center;

    font-family: "Noto Sans KR";

    @media (max-width: 1000px) {
        width: 400px;
        height: 100%;
    }
`

const Signup_main_form = styled.form`
    width: 80%;
    height: 80%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0px;

    border: 1px solid #2C2C2D;
    border-radius: 15px;
    background-color: #1E1E20;

    @media (max-width: 1000px) {
        width: 90%;
    }
`
const Signup_span = styled.span`
    justify-self: flex-start;
    align-self: flex-start;
    margin-left: 10%;
    margin-bottom: 7%;

    font-size: 25px;
    color: white;
    font-weight: bold;

    @media (max-width: 1000px) {
        font-size: 20px;
        margin-left: 5%;
    }
`

const Signup_email_div = styled.div`
    width: 80%;
    height: 12%;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 10px;

    @media (max-width: 1000px) {
        width: 90%;
    }
`
const Signup_email_span = styled.span`
    font-size: 15px;
    font-weight: bold;

    @media (max-width: 1000px) {
        font-size: 12px;
    }
`
const Signup_email_input = styled.input`
    width: 50%;
    height: 50%;

    color: white;
    background-color: #181619;
    font-size: 17px;
    border-radius: 5px;
    border: 1px solid #2C2C2D;

    padding-left: 15px;

    &:focus {
        border-color: #9061F9;
        // input focus   
        outline: none;
    }
`
const Signup_email_verify_button = styled.button`
    width: 25%;
    height: 50%;

    background-color: #9061F9;
    border: none;
    border-radius: 15px;

    font-size: 15px;
    font-weight: 600;
    color: white;

    font-family: "Inter";
    cursor: pointer;

    transition: background-color 0.5s;

    &:hover{
        background-color: #A47CFB;
    }

    @media (max-width: 1000px) {
        width: 25%;
        font-size: 13px;
    }
`
const Signup_secret_div = styled(Signup_email_div)`
`
const Signup_secret_span = styled(Signup_email_span)`
`
const Signup_secret_input = styled(Signup_email_input)`
`
const Signup_secret_verify_button = styled(Signup_email_verify_button)`
    width: 22%;
`

const Signup_name_div = styled(Signup_email_div)`
`
const Signup_name_span = styled(Signup_email_span)`
`
const Signup_name_input = styled(Signup_email_input)`
    width: 65%;
`
const Signup_password_div = styled(Signup_name_div)`
`
const Signup_password_span = styled(Signup_name_span)`
`
const Signup_password_input = styled(Signup_name_input)`
`
const Signup_passwordConfirm_div = styled(Signup_name_div)`
`
const Signup_passwordConfirm_span = styled(Signup_name_span)`
`
const Signup_passwordConfirm_input = styled(Signup_name_input)`
`

const Signup_submit_button = styled.button`
    width: 80%;
    height: 10%;

    background-color: #9061F9;
    border: none;
    border-radius: 15px;

    font-size: 18px;
    font-weight: 600;
    color: white;

    font-family: "Inter";
    cursor: pointer;

    transition: background-color 0.5s;

    &:hover{
        background-color: #A47CFB;
    }

    @media (max-width: 1000px) {
        font-size: 15px;
    }
`

const Signup = () => {
    const [addUser, setAddUser] = useState({
        email: "",
        secret: "",
        name: "",
        password: "",
        passwordConfirm: ""
    });
    const alertOpenRef = useRef(null);
    const verifyBtnRef = useRef(null);
    const emailInputRef = useRef(null);
    const navigate = useNavigate();

    // axios(custom)        .
    //    -  ,    .
    const handleEmailVerifyGet = useCallback(() => {
        const {email} = addUser;
        axiosCustom.post('http://localhost:3002/users/verify', {email})
        .then(res => {
            controlVerifyButton(res);
            alertOpenRef.current.handleOpenAlert(" ", res.data.message);
            return;
        });
    });
    const controlVerifyButton = (res) => {
        if(res.data && res.data.code === 200){
            //          
            verifyBtnRef.current.style.backgroundColor = "#1E1E20";
            verifyBtnRef.current.style.cursor = "default";
            verifyBtnRef.current.disabled = true;
            emailInputRef.current.disabled = true;
        } else {
            verifyBtnRef.current.style.backgroundColor = "#9061F9";
            verifyBtnRef.current.style.cursor = "pointer";
            verifyBtnRef.current.removeAttribute('disabled');
            emailInputRef.current.removeAttribute('disabled');
        }
    };

    //     
    const handleEmailVerifyPost = useCallback(() => {
        const {email, secret} = addUser;
        axiosCustom.post('http://localhost:3002/users/verify/confirm', {email, secret})
        .then(res => {
            alertOpenRef.current.handleOpenAlert(" ", res.data.message);
            return;
        });
    });

    //   -    ,   ,   
    const handleFormSubmit = useCallback((e) => {
        e.preventDefault();
        if(addUser.password.length < 8){
            alertOpenRef.current.handleOpenAlert(" ", " 8  .");
            return;
        }
        else if(!/^(?=.*[A-Za-z])(?=.*\d)(?=.*[@!%*#?&`~$^()_+])[A-Za-z\d@!%*#?&`~$^()_+]{8,}$/.test(addUser.password)){
            alertOpenRef.current.handleOpenAlert(" ", "  +  +    .");
            return;
        }
        if(addUser.password !== addUser.passwordConfirm) {
            alertOpenRef.current.handleOpenAlert(" ", "   .");
            return;
        }
        if(addUser.name.length < 2) {
            alertOpenRef.current.handleOpenAlert(" ", " 2  .");
            return;
        }
        // email, password, name  db  
        const {email, password, name} = addUser;
        axiosCustom.post('http://localhost:3002/users/', {email, password, name})
        .then(res => {
            if(res.data.code === 200){
                navigate('/login');
            }
            alertOpenRef.current.handleOpenAlert(" ", res.data.message);
            return;
        });
    });

    const handleEmailChange = useCallback((e) => {
        setAddUser((current) => {
            const newAddUser = {...current};
            newAddUser.email = e.target.value;
            return newAddUser; 
        });
    });

    const handlePasswordChange = useCallback((e) => {
        setAddUser((current) => {
            const newAddUser = {...current};
            newAddUser.password = e.target.value;
            return newAddUser; 
        });
    });
    const handlePasswordConfirmChange = useCallback((e) => {
        setAddUser((current) => {
            const newAddUser = {...current};
            newAddUser.passwordConfirm = e.target.value;
            return newAddUser; 
        });
    });
    const handlenameChange = useCallback((e) => {
        setAddUser((current) => {
            const newAddUser = {...current};
            newAddUser.name = e.target.value;
            return newAddUser; 
        });
    });
    const handlesecretChange = useCallback((e) => {
        setAddUser((current) => {
            const newAddUser = {...current};
            newAddUser.secret = e.target.value;
            return newAddUser; 
        });
    });

    return (
        <>
            <Alert ref={alertOpenRef} />
            <Signup_Overlay>
                <Signup_Container_div>
                    <Signup_main_form onSubmit={handleFormSubmit}>
                        <Signup_span></Signup_span>
                        <Signup_email_div>
                            <Signup_email_span></Signup_email_span>
                            <Signup_email_input placeholder="your_email@email.com" ref={emailInputRef} onChange={handleEmailChange}/>
                            <Signup_email_verify_button type="button" ref={verifyBtnRef} onClick={handleEmailVerifyGet}></Signup_email_verify_button>
                        </Signup_email_div>
                        <Signup_secret_div>
                            <Signup_secret_span></Signup_secret_span>
                            <Signup_secret_input placeholder=" ." onChange={handlesecretChange}/>
                            <Signup_secret_verify_button type="button" onClick={handleEmailVerifyPost}></Signup_secret_verify_button>
                        </Signup_secret_div>
                        <Signup_name_div>
                            <Signup_name_span></Signup_name_span>
                            <Signup_name_input placeholder="12   " maxLength='12' onChange={handlenameChange}/>
                        </Signup_name_div>
                        <Signup_password_div>
                            <Signup_password_span></Signup_password_span>
                            <Signup_password_input type="password" placeholder=" +  +  8 " onChange={handlePasswordChange}/>
                        </Signup_password_div>
                        <Signup_passwordConfirm_div>
                            <Signup_passwordConfirm_span> </Signup_passwordConfirm_span>
                            <Signup_passwordConfirm_input type="password" placeholder="   " onChange={handlePasswordConfirmChange}/>
                        </Signup_passwordConfirm_div>
                        <Signup_submit_button type="submit"></Signup_submit_button>
                    </Signup_main_form>
                </Signup_Container_div>
            </Signup_Overlay>
        </>
    );
}

export default Signup;
