const MypagePut = () => {
    const [putUser, setputUser] = useState({
        name: "",
        password: "",
        passwordConfirm: ""
    });
    const alertOpenRef = useRef(null);
    const popupOpenRef = useRef(null);
    // main      state  location  
    const location = useLocation();
    const email = location.state.email;
    const dispatch = useDispatch();

    const handleFormSubmit = useCallback((e) => {
        e.preventDefault();
        if(putUser.name.length < 2 && putUser.password.length < 8){
            alertOpenRef.current.handleOpenAlert("  ", "    .");
            return;
        }
        if(putUser.password.length >= 1 && putUser.password.length < 8){
            alertOpenRef.current.handleOpenAlert("  ", "  8  .");
            return;
        }
        if(putUser.password.length >= 8 && 
            !/^(?=.*[A-Za-z])(?=.*\d)(?=.*[@!%*#?&`~$^()_+])[A-Za-z\d@!%*#?&`~$^()_+]{8,}$/.test(putUser.password))
        {
            alertOpenRef.current.handleOpenAlert("  ", "  +  +    .");
            return;
        }
        if(putUser.password !== putUser.passwordConfirm) {
            alertOpenRef.current.handleOpenAlert("  ", "   .");
            return;
        }
        //    (password or name)
        const bodyData = {email: email};
        if(putUser.name.length >= 2){
            bodyData.name = putUser.name;
        }
        if(putUser.password.length >= 8){
            bodyData.password = putUser.password;
        }
        axiosCustom.put('http://localhost:3002/users/', bodyData)
        .then(res => {
            alertOpenRef.current.handleOpenAlert("  ", res.data.message);

            //  nickName     .
            if(res.data && res.data.code === 200){
                dispatch(setNickName({nickName: putUser.name}));
            }
            return;
        });
    });

    const handlePasswordChange = useCallback((e) => {
        setputUser((current) => {
            const newputUser = {...current};
            newputUser.password = e.target.value;
            return newputUser; 
        });
    });
    const handlePasswordConfirmChange = useCallback((e) => {
        setputUser((current) => {
            const newputUser = {...current};
            newputUser.passwordConfirm = e.target.value;
            return newputUser; 
        });
    });
    const handleNameChange = useCallback((e) => {
        setputUser((current) => {
            const newputUser = {...current};
            newputUser.name = e.target.value;
            return newputUser; 
        });
    });

    //   
    const userOut = useCallback((email) => {
        axiosCustom.post('http://localhost:3002/users/deleteByEmail',{email})
        .then(res => {
            alertOpenRef.current.handleOpenAlert("  ", res.data.message);

            //          
            if(res.data && res.data.code === 200){
                const token = document.cookie.split("=")[1];
                if(token && token.length > 0){
                    axiosCustom.get('http://localhost:3002/logout')
                    .then(res => {
                        dispatch(logout());
                        setIsLogined(false);
                    })
                }
            }
            return;
        })
    });
    //     !!! =>  () => ""
    const handleOutUser = useCallback(() => {
        popupOpenRef.current.handleOpenPopup("  ", "  ?    ,   .", () => userOut);
        return;
    });

    return (
        <>
            <Popup parameter={email} ref={popupOpenRef} />
            <Alert ref={alertOpenRef} />
            <MypagePut_Overlay>
                <MypagePut_Container_div>
                    <MypagePut_main_form onSubmit={handleFormSubmit}>
                        <MypagePut_span> </MypagePut_span>
                        <MypagePut_email_div>
                            <MypagePut_email_span></MypagePut_email_span>
                            <MypagePut_email_input disabled value={email}/>
                        </MypagePut_email_div>
                        <MypagePut_name_div>
                            <MypagePut_name_span></MypagePut_name_span>
                            <MypagePut_name_input placeholder="12   " maxLength='12' onChange={handleNameChange}/>
                        </MypagePut_name_div>
                        <MypagePut_password_div>
                            <MypagePut_password_span></MypagePut_password_span>
                            <MypagePut_password_input type="password" placeholder=" +  +  8 " onChange={handlePasswordChange}/>
                        </MypagePut_password_div>
                        <MypagePut_passwordConfirm_div>
                            <MypagePut_passwordConfirm_span> </MypagePut_passwordConfirm_span>
                            <MypagePut_passwordConfirm_input type="password" placeholder="   " onChange={handlePasswordConfirmChange}/>
                        </MypagePut_passwordConfirm_div>
                        <MypagePut_underline_div>
                            <MypagePut_underline_link to="/">HOME</MypagePut_underline_link>
                            <MypagePut_underline_a onClick={handleOutUser}></MypagePut_underline_a>
                        </MypagePut_underline_div>
                        <MypagePut_submit_button type="submit"></MypagePut_submit_button>
                    </MypagePut_main_form>
                </MypagePut_Container_div>
            </MypagePut_Overlay>
        </>
    );
}

export default MypagePut;
