const Mypage_img = styled(Home_img)`
`

// hamburger  
const Hamburger_link = styled(Link)`
    width: 150px;
    height: 40px;
    //      flex-grow 
    flex-grow: 0.5;
    
    display: flex;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    cursor: default;
    background-color: transparent;
    border: none;

    @media (max-width: 1000px) {
        flex-grow: 0.2;
        width: 50px;
    }
`
const Hamburger_Button_img = styled.img`
    width: 50px;
    height: 50px;
    cursor: pointer;

    @media (max-width: 1000px) {
        width: 40px;
        height: 40px;
    }
`

const Main_Header = (props) => {
    // build    !
    //        (user)
    const user = useSelector(state => state.user);
    const dispatch = useDispatch();
    console.log(user)
    const [isLogined, setIsLogined] = useState(false);
    //  (navigate  )     ( )   , 
    //   ,   email, nickName   .
    //  user   (-)  email, nickName   .
    useEffect(() => {
        const token = document.cookie.split("=")[1];
        if(token && token.length > 0){
            // token  ,   email, nickName   .
            dispatch(setToken({token: token}));
            const getuser = async () => {
                const res = await axiosCustom.get('http://localhost:3002/getuser');
                // console.log("getuser res.data.email ", res.data.email);
                dispatch(setEmail({email: res.data.email}));
            }
            getuser();
            const getNickName = async () => {
                const res = await axiosCustom.post('http://localhost:3002/users/email', {email: user.email});
                // console.log("getNickName res.data.data.name ", res.data.data.name);
                dispatch(setNickName({nickName: res.data.data.name}));
            }
            getNickName();
            setIsLogined(true);
        }
    }, [user]);

    const handleLogout = useCallback(() =>{
        //        
        const token = document.cookie.split("=")[1];
        if(token && token.length > 0){
            axiosCustom.get('http://localhost:3002/logout')
            .then(res => {
                dispatch(logout());
                setIsLogined(false);
            })
        }
    });
    //


    const [clickedMenu, setClickedMenu] = useState({
        Home_div: "false",
        Board_div: "false",
        Concert_div: "false",
        Mypage_div: "false"
    });
    const navigate = useNavigate();

    const handleMenuClick = useCallback((e) => {
        setClickedMenu((current) => {
            const newClickedMenu = {...current};
            const target = e.target.name + "_div";
            newClickedMenu[target] = "true";
            return newClickedMenu;
        })
        setTimeout(() => {
            setClickedMenu((current) => {
                const newClickedMenu = {...current};
                const target = e.target.name + "_div";
                newClickedMenu[target] = "false";
                return newClickedMenu;
            })
        }, 1100);
    });

    // hamberger clicked state (app.js props(before using redux))
    const [clickBurger, setClickBurger] = useState("false");
    const handleHamburgerClick = useCallback(() => {
        setClickBurger(() => {
          if(clickBurger === "true"){
            return "false";
          } else {
            return "true";
          }
        });
    });

    const handleMypageNav = useCallback(() => {
        //       
        // navigate   user email (  )
        navigate('/mypage', {state: {email: user.email} });
    });

    return (
        <>
            <Main_Header_Sidebar clickBurger={clickBurger} handleHamburgerClick={handleHamburgerClick}/>
            <Container_div>
                    <Hamburger_link>
                        <Hamburger_Button_img src='/images/hamburger.png' alt='hamburger_button' onClick={handleHamburgerClick} title=' youtube '/>
                    </Hamburger_link>
                    {/* div  Link   id=""    , Link(scroll)   Link(Route)    div  ! */}
                    <ScrollLink to="scroll_1" spy={true} smooth={true}>
                        <Home_div clicked={clickedMenu.Home_div} onClick={handleMenuClick} name="Home" title=''>
                            <Home_img src="/images/home.svg" name="Home"/>
                        </Home_div>
                    </ScrollLink>
                    <ScrollLink to="scroll_2" spy={true} smooth={true}>
                    <Concert_div clicked={clickedMenu.Concert_div} onClick={handleMenuClick} name="Concert" title=''>
                        <Concert_img src="/images/concert.png"  name="Concert"/>
                    </Concert_div>
                    </ScrollLink>
                    <ScrollLink to="scroll_3" spy={true} smooth={true}>
                        <Board_div clicked={clickedMenu.Board_div} onClick={handleMenuClick} name="Board" title=''>
                            <Board_img src="/images/board.png" name="Board"/>
                        </Board_div>
                    </ScrollLink>
                    {isLogined &&
                        <>
                            <Mypage_div clicked={clickedMenu.Mypage_div} onClick={handleMypageNav} name="Mypage" title='  '>
                                <Mypage_img src="/images/human.png" name="Mypage"/>
                            </Mypage_div>
                            <Logout_link onClick={handleLogout}>
                                
                            </Logout_link>
                        </> 
                        ||
                        <>
                            <Login_link to="/login">
                                
                            </Login_link>
                        </>
                    }
            </Container_div>
        </>
    );
}

export default Main_Header;