import React,{forwardRef, useImperativeHandle, useState} from "react";
import styled from "styled-components";

const Alert_Overlay = styled.div`
    //        div 
    //    overlay position absolute,   z-index  up
    position: absolute;
    z-index: 200;
    width: 100%;
    height: 100%;

    /* pc & mobile x scroll OFF~ (body overflow-x: clip) */
    overflow-x: clip;
    background-color: #18181A;
`

const Alert_container_div = styled.div`
    width: 500px;
    height: 250px;
    
    //    
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;

    border: 1px solid #2C2C2D;
    border-radius: 15px;
    color: white;
    background-color: #1E1E20;

    @keyframes popup {
        0%{
            opacity: 0;
        }
        100%{
            opacity: 1;
        }
    }

    @media (max-width: 1000px) {
        width: 350px;
        height: 250px;
    }
`
const Alert_span = styled.span`
    justify-self: flex-start;
    align-self: flex-start;
    margin-left: 10%;

    font-size: 22px;
    color: white;
    font-weight: bold;
`
const Alert_span_text = styled(Alert_span)`
    margin-left: 10%;
    font-size: 15px;
    font-weight: 400;
`
const Alert_button = styled.button`
    width: 25%;
    height: 20%;

    background-color: #9061F9;
    border: none;
    border-radius: 15px;

    font-size: 15px;
    font-weight: 600;
    color: white;

    font-family: "Inter";
    cursor: pointer;

    transition: background-color 0.5s;

    &:hover{
        background-color: #A47CFB;
    }
`

//   ref   forwardRef  
const Alert = forwardRef((props, ref) => {
    const [isAlert, setIsAlert] = useState(false);
    const [span, setSpan] = useState("");
    const [text, setText] = useState("");

    //       useImperativeHandle  
    useImperativeHandle(ref, () => ({
        handleOpenAlert(span, text) {
            setSpan(span);
            setText(text);
            setIsAlert(true);
        },
    }));
    
    const handleCloseAlert = () => {
        setIsAlert(false);
        setSpan("");
        setText("");
    };

    return (
        <>
            {isAlert &&
                <Alert_Overlay>
                    <Alert_container_div style={isAlert ? {animation: "popup 1s"} : {animation: "none"}}>
                        <Alert_span>{span}</Alert_span>
                        <Alert_span_text>{text}</Alert_span_text>
                        <Alert_button onClick={handleCloseAlert}></Alert_button>
                    </Alert_container_div>
                </Alert_Overlay>
            }
        </>
    )
});

export default Alert;