import React,{useRef, useState, useCallback} from "react";
import styled from "styled-components";
import Alert from "../util_components/Alert";
import { useLocation, useNavigate } from "react-router-dom";
import axiosCustom from "../util_components/axiosCustom";

const PasswordChange_Overlay = styled.div`
    //        div , z-index : alert (alert index: 200)
    position: absolute;
    z-index: 105;
    width: 100vw;
    height: 100vh;

    /* pc & mobile x scroll OFF~ (body overflow-x: clip) */
    overflow-x: clip;
    background-color: #18181A;
`

const PasswordChange_Container_div = styled.div`
    width: 700px;
    height: 700px;
    
    // absolute div    
    /* c.f
        + div    -> margin: 0 auto;
        + text    -> text-align: center;
    */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);

    display: flex;
    justify-content: center;
    align-items: center;

    font-family: "Noto Sans KR";

    @media (max-width: 1000px) {
        width: 400px;
        height: 100%;
    }
`

const PasswordChange_main_form = styled.form`
    width: 80%;
    height: 80%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 15px;

    border: 1px solid #2C2C2D;
    border-radius: 15px;
    background-color: #1E1E20;

    @media (max-width: 1000px) {
        width: 90%;
    }
`
const PasswordChange_span = styled.span`
    justify-self: flex-start;
    align-self: flex-start;
    margin-left: 10%;
    margin-bottom: 7%;

    font-size: 25px;
    color: white;
    font-weight: bold;

    @media (max-width: 1000px) {
        font-size: 20px;
        margin-left: 5%;
    }
`

const PasswordChange_email_div = styled.div`
    width: 80%;
    height: 15%;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 10px;

    @media (max-width: 1000px) {
        justify-content: space-between;

        width: 90%;
    }
`
const PasswordChange_email_span = styled.span`
    font-size: 15px;
    font-weight: bold;

    @media (max-width: 1000px) {
        font-size: 12px;
    }
`
const PasswordChange_email_input = styled.input`
    width: 65%;
    height: 50%;

    color: white;
    background-color: #181619;
    font-size: 17px;
    border-radius: 5px;
    border: 1px solid #2C2C2D;

    padding-left: 15px;

    &:focus {
        border-color: #9061F9;
        // input focus   
        outline: none;
    }
`

const PasswordChange_password_div = styled(PasswordChange_email_div)`
`
const PasswordChange_password_span = styled(PasswordChange_email_span)`
`
const PasswordChange_password_input = styled(PasswordChange_email_input)`
`
const PasswordChange_passwordConfirm_div = styled(PasswordChange_email_div)`
`
const PasswordChange_passwordConfirm_span = styled(PasswordChange_email_span)`
`
const PasswordChange_passwordConfirm_input = styled(PasswordChange_email_input)`
`

const PasswordChange_submit_button = styled.button`
    width: 80%;
    height: 10%;

    background-color: #9061F9;
    border: none;
    border-radius: 15px;

    font-size: 18px;
    font-weight: 600;
    color: white;

    font-family: "Inter";
    cursor: pointer;

    transition: background-color 0.5s;

    &:hover{
        background-color: #A47CFB;
    }

    @media (max-width: 1000px) {
        width: 30%;
        font-size: 13px;
    }
`

const PasswordChange = () => {
    //      location.state  
    const location = useLocation();
    const navigate = useNavigate();
    const email = location.state.email;

    const [password, setPassword] = useState({
        password: "",
        passwordConfirm: ""
    });
    const alertOpenRef = useRef(null);

    const handleFormSubmit = useCallback((e) => {
        e.preventDefault();
        if(password.password.length < 8){
            alertOpenRef.current.handleOpenAlert("  ", " 8  .");
            return;
        }
        else if(!/^(?=.*[A-Za-z])(?=.*\d)(?=.*[@!%*#?&`~$^()_+])[A-Za-z\d@!%*#?&`~$^()_+]{8,}$/.test(password.password)){
            alertOpenRef.current.handleOpenAlert("  ", "  +  +    .");
            return;
        }
        if(password.password !== password.passwordConfirm) {
            alertOpenRef.current.handleOpenAlert("  ", "   .");
            return;
        }
        axiosCustom.put('http://localhost:3002/users/',{email, password: password.password})
        .then(res => {
            alertOpenRef.current.handleOpenAlert("  ", res.data.message);
            if(res.data && res.data.code === 200){
                //   
                navigate('/login');
            }
            return;
        })
    });

    const handlePasswordChange = useCallback((e) => {
        setPassword((current) => {
            const newPasswordChange = {...current};
            newPasswordChange.password = e.target.value;
            return newPasswordChange; 
        });
    });
    const handlePasswordConfirmChange = useCallback((e) => {
        setPassword((current) => {
            const newPasswordChange = {...current};
            newPasswordChange.passwordConfirm = e.target.value;
            return newPasswordChange; 
        });
    });

    return (
        <>
            <Alert ref={alertOpenRef} />
            <PasswordChange_Overlay>
                <PasswordChange_Container_div>
                    <PasswordChange_main_form onSubmit={handleFormSubmit}>
                        <PasswordChange_span> </PasswordChange_span>
                        <PasswordChange_email_div>
                            <PasswordChange_email_span></PasswordChange_email_span>
                            <PasswordChange_email_input disabled value={email} />
                        </PasswordChange_email_div>
                        <PasswordChange_password_div>
                            <PasswordChange_password_span></PasswordChange_password_span>
                            <PasswordChange_password_input type="password" placeholder=" +  +  8 " onChange={handlePasswordChange}/>
                        </PasswordChange_password_div>
                        <PasswordChange_passwordConfirm_div>
                            <PasswordChange_passwordConfirm_span> </PasswordChange_passwordConfirm_span>
                            <PasswordChange_passwordConfirm_input type="password" placeholder="   " onChange={handlePasswordConfirmChange}/>
                        </PasswordChange_passwordConfirm_div>
                        <PasswordChange_submit_button type="submit"></PasswordChange_submit_button>
                    </PasswordChange_main_form>
                </PasswordChange_Container_div>
            </PasswordChange_Overlay>
        </>
    );
}

export default PasswordChange;
