import React,{useState, useRef, useEffect} from "react";
import styled from "styled-components";
import Alert from "../util_components/Alert";
import Popup from "../util_components/Popup";
import axiosCustom from "../util_components/axiosCustom";
// redux  user    useSelector
import { useSelector, useDispatch } from 'react-redux';


const Body_container = styled.div`
    //  element component PC   +  80%( / 100vw )
    width: 80%;
    margin: 0 auto;
    height: 1200px;
    margin-top: 600px;

    border: 1px solid white;

    @media (max-width: 1000px) {
        width: 100%;
        margin-top: 150px;
    }
`
const Board_title = styled.p`
    margin-left: 20px;
    font-size: 48px;
    text-shadow: 2px 2px 2px gray;
    font-weight: 600;
    font-family: "Noto Sans KR";

    // mouse scroll   opacity 
    opacity: 1;
    transition: opacity 3s;

    @media (max-width: 1000px) {
        font-size: 37px;
    }
`
const Board_div = styled.div`
    width: 100%;
    height: 85%;
    
    border: 1px solid red;
`
const Search_div = styled.div`
    width: 55%;
    height: 100px;
    display: flex;
    justify-content: space-around;
    align-items: center;
`
const Search_select = styled.select`
    width: 150px;
    height: 40px;
    color: white;
    background-color: #181619;
    font-size: 17px;
    border-radius: 5px;
    border: 1px solid #2C2C2D;

    &:focus {
        border-color: #9061F9;
        // input focus   
        outline: none;
    }
`
const Search_input = styled.input`
    width: 250px;
    height: 40px;
    color: white;
    background-color: #181619;
    font-size: 17px;
    border-radius: 5px;
    border: 1px solid #2C2C2D;

    padding-left: 15px;

    &:focus {
        border-color: #9061F9;
        // input focus   
        outline: none;
    }
`
const Search_icon = styled.img`
    width: 40px;
    height: 40px;

    cursor: pointer;
`
const Forum_button_div = styled.div`
    width: 45%;
    height: 100px;
    display: flex;
    justify-content: space-around;
    align-items: center;
`
const Forum_button_div_button = styled.button`
    width: 25%;
    height: 50%;
    border: none;
    background-color: black;
    border-radius: 15px;

    color: #9061F9;
    font-size: 18px;
    font-weight: bold;
    font-family: 'Noto Sans KR';

    border-radius: 20px;
    cursor: pointer;

    transition: background-color 1s;

    &:hover{
        background-color: white;
    }
`
const Board_list_div = styled.div`
    width: 100%;
    height: 90%;


    border: 1px solid blue;
`
const Board_list_item = styled.div`
    width: 100%;
    height: 8%;
    font-size: 17px;

    display: flex;
    justify-content: space-around;
    align-items: center;
    border: 0.5px solid yellow;
`
const Content_div = styled.div`
    width: 100%;
    height: 5%;
    display: flex;
    justify-content: space-around;
    align-items: center;
` 
const Content_div_writer = styled.input`
    width: 22%;
    height: 80%;
    background-color: black;
    color: white;
    border-radius: 5px;
    border: 1px solid #2C2C2D;
    font-size: 18px;

    padding-left: 15px;

    &:focus {
        border-color: #9061F9;
        // input focus   
        outline: none;
    }
`
const Content_div_title = styled(Content_div_writer)`
    width: 35%;
    height: 80%;
`
const Content_div_date = styled(Content_div_writer)`
    width: 25%;
    height: 80%;
`
const Content_div_back = styled.div`
    width: 5%;
    height: 80%;
    background: url('/images/move-left.png') no-repeat;
    background-position: center;
    cursor: pointer;
`
const Content_text = styled.input`
    width: 95%;
    height: 80%;
    margin-left: 1.5%;
    margin-top: 10px;
    overflow-y: scroll;
    font-size: 25px;

    background-color: black;
    color: white;
    border-radius: 5px;
    border: 1px solid #2C2C2D;

    padding-left: 15px;

    &:focus {
        border-color: #9061F9;
        // input focus   
        outline: none;
    }
`
const Content_sub_div = styled.div`
    width: 95%;
    height: 10%;
    margin: 0 auto;
    margin-top: 10px;

    display: flex;
    justify-content: space-around;
    align-items: center;
`
const Content_sub_div_div = styled.div`
    width: 10%;
    height: 95%;

    display: flex;
    flex-direction: column;
    align-items: center;
`

const Body_7_board = () => {
    const user = useSelector(state => state.user);
    const [posts, setPosts] = useState([]);
    //   select, input  
    const [search, setSearch] = useState({
        select: "",
        input: ""
    });
    //  ,      
    //     // read, write, put, list
    const [mode, setMode] = useState("list");
    //  ,     ,  
    const [text, setText] = useState({
        writer: "",
        title: "",
        content: "",
        updateAt: "",
        up: 0,
        down: 0,
    });

    //    (1page)  
    useEffect(() => {
        const getdata = async () => {
            await axiosCustom.get('/post/getallposts',{nowpage: -1})
            .then(res => {
                setPosts(res.data.posts)
            });
        };
        getdata();
    },[])


    //    ref 
    const popupOpenRef = useRef(null);
    const alertOpenRef = useRef(null);

    //  select   
    const selectChangeHandle = (e) => {
        setSearch((current) => {
            const newSearch = {...current};
            newSearch.select = e.target.value;
            return newSearch;
        });
    };
    //  state  
    const inputChangeHandle = (e) => {
        setSearch((current) => {
            const newSearch = {...current};
            newSearch.input = e.target.value;
            return newSearch;
        });
    };
    //   
    const searchHandle = () => {
        console.log("searchHandle")
    };
    //      
    const inputEnterHandle = (e) => {
        if(e.key === "Enter"){
            searchHandle();
        }
    };
    //      
    const backHandle = () => {
        setMode("list");
    };
    //    
    const writeStartHandle = () => {
        if(user.email.length < 1){
            alertOpenRef.current.handleOpenAlert(" ", "  .");
            return;
        }

        setMode("write");

        setText((current) => {
            const newSetText = {...current};
            newSetText.title = "";
            newSetText.content = "";
            return newSetText;
        })
    };
    //     
    const writePutHandle = () => {
        setMode("put");

        setText((current) => {
            const newSetText = {...current};
            newSetText.title = "nanoid_hide_title";
            newSetText.content = "nanoid_hide_title_text";
            return newSetText;
        })
    };
    // ,   text   
    const writeChangeHandle = (e) => {
        if(e.target.name === "title"){
            setText((current) => {
                const newSetText = {...current};
                newSetText.title = e.target.value;
                return newSetText;
            });
        } else {
            setText((current) => {
                const newSetText = {...current};
                newSetText.content = e.target.value;
                return newSetText;
            });
        }
    };

    //  
    const postWriteHandle = () => {
        if(user.email.length < 1){
            alertOpenRef.current.handleOpenAlert(" ", "  .");
            return;
        }
        if(text.title.length > 20){
            alertOpenRef.current.handleOpenAlert(" ", "  20  .");
            return;
        }
        if(text.title.length < 3 || text.content.length < 3){
            alertOpenRef.current.handleOpenAlert(" ", "    3  .");
            return;
        }
        axiosCustom.post('/post/write',{email: user.email, title: text.title, content: text.content})
        .then(res => {
            alertOpenRef.current.handleOpenAlert(" ", res.data.message);
            if(res.data.code === 200){
                backHandle();
            }
            return;
        })
    }

    //    
    const postReadHandle = (e) => {
        axiosCustom.get('/post/get',{nanoid: e.target.name})
        .then(res => {
            alertOpenRef.current.handleOpenAlert(" ", res.data.message);
            if(res.code === 200){
                setMode("read");
                setText((current) => {
                    const newSetText = {...current};
                    newSetText.writer = res.data.author.name;
                    newSetText.title = res.data.title;
                    newSetText.content = res.data.content;
                    newSetText.updateAt = res.data.updateAt;
                    newSetText.up = res.data.up;
                    newSetText.down = res.data.down;
                    return newSetText;
                })
            }
            return;
        })
    }

    return (
        <>
        <div style={{marginTop: "600px"}}>
            <Popup parameter={"put or delete  nanoid"} ref={popupOpenRef} />
            <Alert ref={alertOpenRef} />
        </div>
        <Body_container>
            <Board_title>Open Forum</Board_title>
            <Board_div>
                {mode === "read" 
                &&
                <>
                    <Content_div>
                        <Content_div_back onClick={backHandle}></Content_div_back>
                        <Content_div_writer style={{border:"none"}} disabled defaultValue={text.writer}/>
                        <Content_div_title name="nanoid_hide" style={{border:"none"}} disabled defaultValue={text.title}/>
                        <Content_div_date style={{border:"none"}} disabled defaultValue={text.updateAt}/>
                    </Content_div>
                    <Content_text style={{border:"none"}} disabled defaultValue={text.content}/>
                    <Content_sub_div>
                        <Content_sub_div_div>
                            <img src="/images/good.png" style={{width:"50px", height:"50px", cursor:"pointer"}} />
                            <span></span>
                            <span>{text.up}</span>
                        </Content_sub_div_div>
                        <Content_sub_div_div>
                            <img src="/images/bad.png" style={{width:"50px", height:"50px", cursor:"pointer"}} />
                            <span></span>
                            <span>{text.down}</span>
                        </Content_sub_div_div>

                        <Content_sub_div_div>
                            <img src="/images/modify.png" onClick={writePutHandle} style={{width:"50px", height:"50px", cursor:"pointer"}} />
                            <span></span>
                        </Content_sub_div_div>
                        <Content_sub_div_div>
                            <img src="/images/delete.png" style={{width:"50px", height:"50px", cursor:"pointer"}} />
                            <span></span>
                        </Content_sub_div_div>
                    </Content_sub_div>
                </>
                }
                {mode === "write" 
                &&
                <>
                    <Content_div>
                        <Content_div_back onClick={backHandle}></Content_div_back>
                        <Content_div_writer disabled value={user.email.length >= 1 ? user.nickName : "nonLogin"} />
                        <Content_div_title onChange={writeChangeHandle} name="title" />
                    </Content_div>
                    <Content_text onChange={writeChangeHandle} name="content" />
                    <Content_sub_div>
                        <Content_sub_div_div>
                            <img src="/images/write.png" onClick={postWriteHandle} style={{width:"50px", height:"50px", cursor:"pointer"}} />
                            <span style={{marginTop:"5%"}}></span>
                        </Content_sub_div_div>
                    </Content_sub_div>
                </>
                }
                {mode === "put" 
                &&
                <>
                    <Content_div>
                        <Content_div_back onClick={backHandle}></Content_div_back>
                        <Content_div_writer disabled value="read_writer"/>
                        {/* react   input value   defaultValue    */}
                        <Content_div_title onChange={writeChangeHandle} name="title" defaultValue={text.title} />
                        <Content_div_date disabled value="read_date"/>
                    </Content_div>
                    <Content_text onChange={writeChangeHandle} name="content" defaultValue={text.content}/>
                    <Content_sub_div>
                        <Content_sub_div_div>
                            <img src="/images/write.png" style={{width:"50px", height:"50px", cursor:"pointer"}} />
                            <span style={{marginTop:"5%"}}></span>
                        </Content_sub_div_div>
                    </Content_sub_div>
                </>
                }
                {mode === "list" 
                &&
                <>
                    <div style={{display:"flex"}}>
                        <Search_div>
                            <Search_select onChange={selectChangeHandle}>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                            </Search_select>
                            <Search_input onKeyDown={inputEnterHandle} onChange={inputChangeHandle}></Search_input>
                            <Search_icon src="/images/search.png" onClick={searchHandle}></Search_icon>
                        </Search_div>
                        <Forum_button_div>
                            <Forum_button_div_button></Forum_button_div_button>
                            <Forum_button_div_button>  </Forum_button_div_button>
                            <Forum_button_div_button onClick={writeStartHandle}></Forum_button_div_button>
                        </Forum_button_div>
                    </div>
                    <Board_list_div>
                        {posts.map((v) => {
                                return (
                                    <Board_list_item>
                                        <span style={{width:"20%"}}>{v.writer}</span>
                                        <span name={v.nanoid} onClick={postReadHandle} style={{width:"40%", textDecoration:"underLine", cursor:"pointer"}}>{v.title}</span>
                                        <span style={{width:"30%"}}>{v.updateAt}</span>
                                    </Board_list_item>
                                );
                            })
                        }
                    </Board_list_div>
                </>    
                }
            </Board_div>
        </Body_container>
        </>
    )

}


export default Body_7_board;