import axios from 'axios';

const axiosCustom = axios.create({
  baseURL: 'http://localhost:3002',
  timeout: 10000,
});

// axios      Alert    -> interceptors
axiosCustom.interceptors.response.use(
  (response) => {
    return response;
  },
  (e) => {
    return e.response;
  }
);

export default axiosCustom;